<template>
  <div class="commerceOrderDetail">
    <navigation-bar pageName="订单详情" right-text @onLeftClick="onClickLeft" />

    <!-- 商品信息 -->
    <div class="commerce-over-box" :style="{ bottom: orderInfo.billStatus=='待收货' ? '1.6rem' : 0 }">
    <div class="card" v-if="receivedInfo && (receivedInfo.receivedName || receivedInfo.receivedPhone)">
      <div class="detailTypePanel">
        <span class="detailTypeName">送达人</span>
      </div>
      <div class="van-hairline--top"></div>
      <div class="detailPersonPanel" >
        <div class="person-info-div">
          <div class="person-name-div">
            <div class="personName">{{receivedInfo.receivedName}}</div>
            <div class="personPhone">{{receivedInfo.receivedPhone}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="card">
      <div class="detailTypePanel">
        <span class="detailTypeName">商品信息</span>
        <div v-if="orderInfo.deliveryType==0" class="SendTypeDelivery">
          <div class="sendType">快递配送</div>
        </div>
        <div v-if="orderInfo.deliveryType==1" class="SendTypeDelivery">
          <div class="sendType">销售配送</div>
        </div>
        <div v-if="orderInfo.deliveryType==2" class="SendTypeSelf">
          <div class="sendType">到店自提</div>
        </div>
      </div>
      <div class="van-hairline--top"></div>
      <div class="detailGoodsPanel" v-for="(good,index) in orderInfo.goodsInfo" :key="index">
        <div class="goods-info-div">
          <div class="goods-name-div">
            <div class="orderGoods">{{good.goodsName}}</div>
            <div class="orderPrice">￥{{good.skuPrice}}</div>
          </div>
          <div class="goods-num-div">
            <div class="orderSpecs">{{good.attrValueContent}}</div>
            <div class="orderGoodsNum">x{{good.skuCount}}</div>
          </div>
        </div>
      </div>

      <div class="dashLine" style="margin: 10px 15px 0 15px;;"></div>
      <div class="detailInfoPanel">
        <div class="detailInfoItem">
          <span class="detailInfoTitle" style="color: #333333">配送费</span>
          <span class="infoDetai">￥{{orderInfo.deliveryPrice}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle" style="color: #333333">实际支付</span>
          <span class="infoDetai" style="color: #F43D3D">￥{{orderInfo.realPayTotal}}</span>
        </div>
      </div>
    </div>

    <!-- 店铺信息 -->
    <div class="card">
      <div class="detailTypePanel">
        <span class="detailTypeName">店铺信息</span>
      </div>
      <div class="van-hairline--top"></div>
      <div class="detailInfoPanel">
        <div class="detailInfoItem">
          <span class="detailInfoTitle">店铺名称</span>
          <span class="infoDetai">{{orderInfo.shopName}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle">供应商</span>
          <span class="infoDetai">{{orderInfo.supplierName}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle">供应商电话</span>
          <span class="infoDetai">{{orderInfo.supplierPhone}}</span>
        </div>
      </div>
    </div>

    <!-- 配送信息 -->
    <div class="card">
      <div class="detailTypePanel">
        <span class="detailTypeName">配送信息</span>
      </div>
      <div class="van-hairline--top"></div>
      <div class="detailInfoPanel">
        <div class="detailInfoItem">
          <span class="detailInfoTitle">收货地址</span>
          <span class="infoDetai">{{orderInfo.recAddr}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle">收货人</span>
          <span class="infoDetai">
            {{orderInfo.recName}}
            {{orderInfo.recPhone}}
          </span>
        </div>
      </div>
    </div>

    <!-- 订单信息 -->
    <div class="card">
      <div class="detailTypePanel">
        <span class="detailTypeName">订单信息</span>
      </div>
      <div class="van-hairline--top"></div>
      <div class="detailInfoPanel">
        <div class="detailInfoItem">
          <span class="detailInfoTitle">订单号</span>
          <span class="infoDetai">{{orderInfo.subBillNo}}</span>
        </div>
        <div class="detailInfoItem" v-if="orderInfo.deliveryDate">
          <span class="detailInfoTitle">配送时间</span>
          <span class="infoDetai">{{orderInfo.deliveryDate}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle">下单时间</span>
          <span class="infoDetai">{{orderInfo.createdTime}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle">支付方式</span>
          <span class="infoDetai">{{orderInfo.payType}}</span>
        </div>
        <div class="detailInfoItem">
          <span class="detailInfoTitle">订单状态</span>
          <span class="infoDetai">{{orderInfo.billStatus}}</span>
        </div>
      </div>
    </div>
    </div>
    <div class="receiveCommonPanel" v-if="orderInfo.billStatus=='待收货'">
      <div class="receiveBtn" @click="received(orderInfo)">已送达</div>
      <div class="receiveBtn" v-if="orderInfo.noticeTimes && orderInfo.noticeTimes <= 0" @click="goodArrive(orderInfo)">已接货</div>
      <div class="receiveBtn-disabled" v-else-if="orderInfo.noticeTimes && orderInfo.noticeTimes >= 1">已接货</div>
      <div class="receiveBtn" v-else @click="goodArrive(orderInfo)">已接货</div>
      <div class="receiveBtn" v-if="dadaExpressSurported && orderInfo.expressCompany && (orderInfo.expressCompany.indexOf('达达') != -1)" @click="getDadaExpressInfo(orderInfo)">查看物流</div>
      <!-- <div class="receiveBtn" @click="getDadaExpressInfo(orderInfo)">查看物流</div> -->
    </div>
  </div>
</template>

<script>
import {
  getOrderListService,
  sendGoodsArriveMsg,
  setOrderReceived,
  getDadaOrderTrack,
} from "../../services/commerceService.js";
import { store, storage } from "../../utils/store";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commerceOrderDetail",
  components: { NavigationBar },
  data() {
    return {
      loading: false,
      finished: false,
      orderInfo: {},
      dadaExpressSurported: true,
      receivedInfo: {receivedName: "", receivedPhone: ""},
    };
  },
  activated() {
    //获取用户数据
    this.userInfo = store.get("userInfo");
    this.userInfo = this.userInfo
      ? this.userInfo
      : storage.getStorage("custNews");
    //获取订单数据
    if (this.$route.query.orderInfo.subBillNo) {
      this.orderInfo = this.$route.query.orderInfo;
    }
    this.receivedInfo = this.$route.query.receivedInfo;
  },
  methods: {
    //订单收货按钮事件
    received(orderInfo) {
      var data = {
        subBillNo: orderInfo.subBillNo,
        signerType: "03",
        signerCode: this.userInfo.USER_CODE
      };
      setOrderReceived(data)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            store.get("dealFunction")(this.orderInfo.subBillNo);
            this.orderInfo.billStatus = "已完成";
          } else {
            result.msg
                ? this.$toast(result.msg):this.$toast("请求失败，请稍后重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    //到货通知按钮事件
    goodArrive(orderInfo) {
      if (this.dadaExpressSurported && orderInfo.noticeTimes && orderInfo.noticeTimes >= 1) {
        console.log("goods already arrived, need not request, ignore.");
        return;
      }
      var data = {
        subBillNo: orderInfo.subBillNo,
        userPhone: this.userInfo.PHONE
      };
      sendGoodsArriveMsg(data)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            this.$toast("发送成功");
            orderInfo.noticeTimes = 1;
          } else {
            result.msg
                ? this.$toast(result.msg):this.$toast("请求失败，请稍后重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onClickLeft() {
      this.$router.back();
    },
    getDadaExpressInfo(orderInfo) {
      this.$router.push({ name: "mapForExpress", params: { subBillNo: orderInfo.subBillNo } });
        // getDadaOrderTrack(orderInfo.subBillNo)
        //   .then(result => {
        //     console.log("the track result " + result);
        //     // if is dada express
        //     // show dada track url in new page
        //     this.$toast("the track result " + JSON.stringify(result));
        //     let url = 'https://www.baidu.com';
        //     cordova.InAppBrowser.open(
        //             url,
        //     );
        //   })
        //   .catch(err => {
        //     console.log("get dada order info exception: " + err);
        //     this.$toast(err);
        //   })

          // for debug
          // let url = 'https://www.baidu.com';
          // cordova.InAppBrowser.open(
          //         url,
          // );
    },
  }
};
</script>

<style>
.commerceOrderDetail {
  background-color: #f2f2f2;
  height: 100%;
  text-align: left;
  padding: 50px 0 64px 0;
  box-sizing: border-box;
}

.commerceOrderDetail .commerce-over-box {
  position: absolute;
  top: 44px;
  bottom: 60px;
  width: 100%;
  overflow: auto;
}

.card {
  padding-top: 1px;
  padding-bottom: 1px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 10px;
  background-color: #fff;
}

.card .dashLine {
  border: #e5e5e5 0.5px dashed;
}

/* 订单详情其它信息样式 */
.commerceOrderDetail .detailTypePanel {
  height: 40px;
  line-height: 40px;
  width: 100%;
}

.commerceOrderDetail .detailTypePanel .detailTypeName {
  font-size: 14px;
  font-weight: bold;
  color: #333333;
  letter-spacing: -0.04px;
  border-left: #00d8d1 3px solid;
  padding-left: 5px;
  margin: 10px 0 10px 15px;
}

/* 订单详情送达人信息样式 */
.detailPersonPanel {
  height: auto;
  padding-top: 10px;
  display: flex;
}

.detailPersonPanel .person-info-div {
  width: 100%;
  height: auto;
}

.detailPersonPanel .person-info-div .person-name-div {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.detailPersonPanel .personName {
  font-size: 14px;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: 250px;
  /*white-space: nowrap;*/
  overflow: hidden;
  text-overflow: ellipsis;
  text-overflow: -o-ellipsis-lastline;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
}

.detailPersonPanel .personPhone {
  margin-right: 15px;
  font-size: 14px;
  color: #333333;
  flex-grow: 1;
  text-align: right;
}

/* 订单详情商品信息样式 */
.detailGoodsPanel {
  height: auto;
  padding-top: 10px;
  display: flex;
}

.detailGoodsPanel .goods-info-div {
  width: 100%;
  height: auto;
}

.detailGoodsPanel .goods-info-div .goods-name-div {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.detailGoodsPanel .goods-info-div .goods-num-div {
  width: 100%;
  display: flex;
  padding-bottom: 10px;
}

.detailGoodsPanel .orderGoods {
  font-size: 14px;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: 250px;
  /*white-space: nowrap;*/
  overflow: hidden;
  text-overflow: ellipsis;
  text-overflow: -o-ellipsis-lastline;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
}

.detailGoodsPanel .orderSpecs {
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.detailGoodsPanel .orderGoodsNum {
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: 20px;
}

.detailGoodsPanel .orderPrice {
  margin-right: 15px;
  font-size: 14px;
  color: #333333;
  flex-grow: 1;
  text-align: right;
}

/* 内容详情 */
.detailInfoPanel {
  width: 100%;
  padding-bottom: 10px;
}

.detailInfoPanel .detailInfoItem {
  /* height: 18px; */
  margin-top: 10px;
  margin-left: 15px;
  margin-right: 15px;
  display: flex;
}

.detailInfoPanel .detailInfoTitle {
  font-size: 14px;
  color: #888888;
  letter-spacing: -0.04px;
  width: 120px;
}

.detailInfoPanel .infoDetai {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
  text-align: right;
  float: right;
  flex-grow: 1;
}

/* 收货按钮 */
.commerceOrderDetail .receivePanel {
  height: 45px;
}

.commerceOrderDetail .receivePanel .receiveBtn {
  background: #00d8d1;
  border: 1px solid #00d8d1;
  box-shadow: 0 0 8px 0 rgba(0, 158, 161, 0.32);
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  float: right;
  margin-right: 15px;
  font-size: 14px;
}

.commerceOrderDetail .receivePanel .receiveBtn-disabled {
  background: #888;
  border: 1px solid #888;
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  float: right;
  margin-right: 15px;
  font-size: 14px;
}

/* 发货方式tip */
.commerceOrderDetail .SendTypeSelf {
  position: absolute;
  right: 10px;
  top: 0px;
  height: 40px;
  width: 40px;
  background-color: #f5a623;
  border-radius: 0 5px;
}

.commerceOrderDetail .SendTypeDelivery {
  position: absolute;
  right: 10px;
  top: 0px;
  height: 40px;
  width: 40px;
  background-color: #8ab7ff;
  border-radius: 0 5px;
}

.commerceOrderDetail .sendType {
  font-size: 10px;
  line-height: 12px;
  width: 30px;
  height: 30px;
  text-align: center;
  margin: 10px 5px 0 5px;
  color: white;
}

/* 收货按钮 */
.receiveCommonPanel {
  height: 60px;
  line-height: 60px;
  width: 100%;
  bottom: 0;
  text-align: center;
  position: fixed;
  background-color: white;
  box-shadow: 0 -2px 7px 0 rgba(226, 226, 226, 0.5);
}

.receiveCommonPanel .receiveBtn {
  background: #00d8d1;
  border: 1px solid #00d8d1;
  box-shadow: 0 0 8px 0 rgba(0, 158, 161, 0.32);
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  font-size: 16px;
  float: right;
  margin-right: 15px;
  margin-top: 15px;
}

.receiveCommonPanel .receiveBtn-disabled {
  background: #888;
  border: 1px solid #888;
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  float: right;
  margin-right: 15px;
  margin-top: 15px;
  font-size: 16px;
}
</style>
